.App {
  width: 100%;
  background: #fdfdfd;
  font-family: Poppins;
  font-style: normal;
  font-weight: normal;
  color: #000000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: space-between;
}

.logo {
  float: left;
  width: 64px;
  height: 64x;
  margin: 0;
}

.site-layout .site-layout-background {
  background: #fdfdfd;
}

.app-container {
  margin: auto;
  max-width: 100%;
  width: 1200px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-content: center;
  background-color: #fdfdfd;
  padding: 24px;
}

.contacts {
  display: flex;
  flex-direction: column;
}

.contact {
  margin-left: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.contact-url {
  font-size: 18 px;
  line-height: 27px;
  color: #000000;
  text-decoration: none;
}

.fa {
  margin: 10px;
}

@primary-color: #e6f7ff;@link-color: #1890ff;@success-color: #52c41a;@warning-color: #faad14;@error-color: #f5222d;@font-size-base: 14px;@heading-color: rgba(0, 0, 0, 0.85);@text-color: rgba(0, 0, 0, 0.65);@text-color-secondary: rgba(0, 0, 0, 0.45);@disabled-color: rgba(0, 0, 0, 0.25);@border-radius-base: 2px;@border-color-base: #d9d9d9;@box-shadow-base: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);